'use strict';

/* global yith_wcaf */

import YITH_WCAF_Admin_Actions_Table from './yith-wcaf-admin-actions-table';
import YITH_WCAF_Add_Rate_Rule_Modal from './yith-wcaf-add-rate-rule-modal';

export default class YITH_WCAF_Rate_Rules_Table extends YITH_WCAF_Admin_Actions_Table {
	constructor( $context, args ) {
		super( $context, {
			action_suffix: '_rate_rule',
			nonce_suffix: '_rate_rule',
			data: ( $row ) => {
				return {
					rule_id: $row.data( 'id' ),
				};
			},
			...args,
		} );
	}

	initModal() {
		const $openers = this.getModalOpeners();

		if ( $openers.length ) {
			this.modal = new YITH_WCAF_Add_Rate_Rule_Modal( $openers, {
				onSubmitSuccess: ( data ) => {
					if ( ! data?.template ) {
						return;
					}

					this.replaceTable( data?.template );
				},
			} );
		}
	}
}
