'use strict';

/* global yith_wcaf */

import { labels } from '../../../src/globals.js';
import YITH_WCAF_Admin_Modal from './yith-wcaf-admin-modal';
import YITH_WCAF_Ajax from '../../../src/modules/ajax.js';

export default class YITH_WCAF_Add_Rate_Rule_Modal extends YITH_WCAF_Admin_Modal {
	constructor( $opener, args ) {
		super( $opener, {
			template: 'yith-wcaf-add-rate-rule-modal',
			title: ( data ) => {
				return data.name
					? labels?.edit_rate_rule_title
					: labels?.add_rate_rule_title;
			},
			data() {
				const $target = this.$target,
					$dataSource = $target?.closest( '[data-item]' );

				if ( ! $dataSource || ! $dataSource.length ) {
					return {};
				}

				return $dataSource.data( 'item' );
			},
			width: 400,
			...args,
		} );
	}

	onSubmit() {
		const $content = this.modal.elements.content;

		if ( ! this.beforeSubmit() ) {
			return false;
		}

		this.hideErrorMessage();

		YITH_WCAF_Ajax.post
			.call( $content, 'save_rate_rule', 'save_rate_rule', {
				rule: this.serialize(),
			} )
			.done( ( data ) => {
				if ( 'function' === typeof this.args?.onSubmitSuccess ) {
					this.args?.onSubmitSuccess( data?.data );
				}

				if ( data?.success ) {
					this.modal.close();
				} else if ( data?.data?.message ) {
					this.showErrorMessage( data.data.message );
				}
			} );

		return false;
	}
}
